import {AppRegistry} from 'react-native';
import {name as appName} from './app.json';
import WebApp from './src/WebApp.bs.js';
if (module.hot) {
  module.hot.accept();
}
AppRegistry.registerComponent(appName, () => WebApp);

// function handleMessage(ev) {
//   console.log('got 2 event form dashboard', ev.data);
// }
// window.addEventListener('message', handleMessage);

const runApp = async () => {
  let props = {};

  if (window.location.href == 'http://localhost:8080/') {
    let response = await fetch(
      'https://u4kkpaenwc.execute-api.ap-south-1.amazonaws.com/default/create-payment-intent',
      {
        method: 'POST',
        body: JSON.stringify({
          amount: 6541,
          currency: 'USD',
          confirm: false,
          capture_method: 'automatic',
          authentication_type: 'no_three_ds',
        }),
      },
    );

    const data = await response.json();

    props = {
      local: true,
      publishableKey: data.publishableKey,
      configuration: {
        allowsDelayedPaymentMethods: true,
        merchantDisplayName: 'Example, Inc.',
        allowsPaymentMethodsRequiringShippingAddress: false,
        googlePay: {
          environment: 'Test',
          countryCode: 'US',
          currencyCode: 'US',
        },
      },
      appId: 'com.example.nativeapp',
      type: 'payment',
      clientSecret: data.clientSecret,
    };
  }

  AppRegistry.runApplication(appName, {
    initialProps: {props},
    rootTag: document.getElementById('app-root'),
  });
  window.parent.postMessage(
    JSON.stringify({
      sdkLoaded: true,
    }),
    '*',
  );
};

runApp();
